import React from 'react'
import './point.css'

import io from 'socket.io-client'
const socket = io('http://localhost:63005')

function Enter ({ history }) {

  const $name = React.createRef()
  const $pin = React.createRef()

  const enter = () => {

    const data = {
      enique__name: $name.current.value,
      secure__pin: $pin.current.value
    }

    socket.emit('enter', data, (data) => {

       console.log(data)

       history.push('/profile')
    })

  }

  return (
    <div className='area__enter'>
      <input ref={$name} className='inp__enter' type='text' placeholder='unique name' />
      <input ref={$pin} className='inp__enter' type='text' placeholder='secure pin' />
      <button className='submit__enter' onClick={enter}>
        <span> Enter </span>
      </button>
    </div>
  )

}

export default Enter
